.list {
    display: flex;
    list-style: none;
    justify-content: center;
}

.item {
    margin-inline-end: 100px;
    align-items: center;
    list-style: none; 
}

.item a{
    text-decoration: none;
    color: black;
}

.item a:hover{
    font-size: 40px;
    color: blue;
}

.navbar{
    background-color:forestgreen;
    padding: 10px;
}

.disabled{
    pointer-events: none;
    color: grey;
}